ul {
  width: min-content;
  height: 100%;
  background-color: #808080;
  display: grid;
  grid-template: repeat(5, auto) / repeat(7, auto);
  gap: 20px;
  margin: 100px auto;
  padding: 90px 30px 0;
  position: relative;
  font-family: "Abril";
  color: #1A1A1A;
  color: #fff;
  
  li {
    width: 50px;
    height: 50px;
    font-size: 22px;
    text-align: center;
    background-color: #808080;
    border: 2px solid rgba(26, 26, 26, 0.6);
    z-index: 100;
    /* border: 3px solid #6b6b6b; */
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all .1s;

    &.week{
      font-family: Helvet;
    }

    &.now_month {
      background-color: rgb(63, 63, 63);
    }

    &.today {
      background-color: rgb(42, 73, 41);
    }

    &:hover {
      color: #fff;
      font-size: 25px;
      border: 3px solid #f0f0f0;
    }
  }

  &>div.title {
    width: 400px;
    height: 50px;
    position: absolute;
    font-size: 40px;
    color: #e4e4e4;
    z-index: 100;
    top: 20px;
    left: 30px;
  }
}

.noselect {
	color: #555;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}