button {
  content: "";
  border: none;
  transition: all .3s;
  background-color: transparent;
  position: absolute;
  top: 30px;
  z-index: 100;
  transform: translateY(50%, 50%);
  outline: none;

  path{
    transition: all .3s;
  }

  &:hover {
    path{
      fill: #e4e4e4;
    }
  }

  &.button__prev{
    right: 110px;
  }

  &.button__next{
    right: 35px;
    top: 27px;
    transform: rotateZ(180deg);
  }
}