@font-face {
  font-family: "Abril";
  font-weight: 500;
  font-style: normal;  
  src:local(Abril),  
      url("./Fonts/AbrilFatface-Regular.woff2") format("woff2"),
      url("./Fonts/AbrilFatface-Regular.woff") format("woff"),
      url("./Fonts/AbrilFatface-Regular.eot") format("eot");
  font-display:swap;
}
@font-face {
  font-family: "Helvet";
  font-weight: 300;
  font-style: light;  
  src:local(Helvet),  
      url("./Fonts/HelveticaNeue-Light.otf") format("otf");
  font-display:swap;
}
@font-face {
  font-family: "Helvet";
  font-weight: 500;
  font-style: medium;  
  src:local(Helvet),  
      url("./Fonts/HelveticaNeue-Medium.otf") format("otf");
  font-display:swap;
}
@font-face {
  font-family: "Helvet";
  font-weight: 700;
  font-style: bold;  
  src:local(Helvet),  
      url("./Fonts/HelveticaNeue-Bold.otf") format("otf");
  font-display:swap;
}